.header {
  background-color: #2d3336;
  padding: 0;
}
.header__wrap {
  display: flex;
  align-items: center;
}
.header .logo {
  transform: translateY(5px);
}
.header .logo svg {
  fill: #fff;
  width: 133px;
  height: 71px;
}
.header .menu {
  list-style: none;
  padding: 0;
  margin: 0;
  color: #fff;
  font-size: 17px;
}
.header .menu__item .toggle {
  position: absolute;
  width: 20px;
  height: 20px;
  text-align: center;
  line-height: 16px;
  transition: 0.3s;
}
.header .menu__item .toggle svg {
  width: 14px;
  height: 8px;
  fill: #fff;
}
.header .menu__item .toggle.active {
  transform: rotate(180deg);
}
.header .menu.desktop {
  display: flex;
  margin-left: auto;
  margin-right: 37px;
}
.header .menu.desktop .menu__item {
  position: relative;
  padding: 40px 16px;
  transition: 0.3s;
}
.header .menu.desktop .menu__item.dropdown {
  padding-right: 35px;
}
.header .menu.desktop .menu__item:not(:last-of-type) {
  margin-right: 37px;
}
.header .menu.desktop .menu__item > .toggle {
  right: 7px;
  top: 42px;
}
.header .menu.desktop .menu__item:hover {
  background-color: #b0c468;
}
.header .menu.desktop .menu__item:hover > .toggle {
  transform: rotate(180deg);
}
.header .menu.desktop .menu__item:hover > .sub {
  opacity: 1;
  visibility: visible;
}
.header .menu.desktop .sub {
  position: absolute;
  z-index: 2;
  top: 100%;
  left: 0;
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
  background-color: #b0c468;
  transition: 0.3s;
  opacity: 0;
  visibility: hidden;
  font-size: 15px;
}
.header .menu.desktop .sub__item {
  min-width: 110px;
  padding: 10px;
  text-align: center;
  transition: 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.header .menu.desktop .sub__item.dropdown {
  padding-right: 30px;
  min-width: 130px;
}
.header .menu.desktop .sub__item > .toggle {
  right: 7px;
  top: 0;
  bottom: 0;
  margin: auto;
}
.header .menu.desktop .sub__item:hover {
  background-color: #2d3336;
}
.header .menu.desktop .sub__item:hover > .toggle {
  transform: rotate(180deg);
}
.header .menu.desktop .sub__item:hover > .subsub {
  opacity: 1;
  visibility: visible;
}
.header .menu.desktop .subsub {
  position: absolute;
  z-index: 2;
  top: 100%;
  left: 0;
  list-style: none;
  padding: 0;
  margin: 0;
  background-color: #b0c468;
  transition: 0.3s;
  opacity: 0;
  visibility: hidden;
  font-size: 15px;
}
.header .menu.desktop .subsub__item {
  min-width: 180px;
  padding: 10px;
  transition: 0.3s;
}
.header .menu.desktop .subsub__item:hover {
  background-color: #2d3336;
}
.header .menu.mobile {
  display: block;
  max-width: 230px;
  width: 100%;
}
.header .menu.mobile .menu__item {
  position: relative;
  padding: 10px 0;
}
.header .menu.mobile .menu__item > .toggle {
  right: 0;
  top: 10px;
}
.header .menu.mobile .menu__item > a {
  margin-right: 10px;
}
.header .menu.mobile .sub {
  display: none;
  background-color: transparent;
  list-style: none;
  padding: 0;
  margin: 0;
  font-size: 15px;
}
.header .menu.mobile .sub__item {
  padding: 10px 0 10px 10px;
  position: relative;
}
.header .menu.mobile .sub__item > .toggle {
  position: absolute;
  right: 0;
  top: 9px;
}
.header .menu.mobile .sub__item > a {
  margin-right: 10px;
}
.header .menu.mobile .subsub {
  display: none;
  position: static;
  background-color: transparent;
  list-style: none;
  padding: 0;
  margin: 0;
  font-size: 15px;
}
.header .menu.mobile .subsub__item {
  padding: 10px;
}
.header .phone {
  font-size: 17px;
  color: #fff;
}
.header .btn {
  font-size: 17px;
  padding: 11px 17px;
  margin-left: 46px;
}
.header .burger {
  margin-left: auto;
  width: 30px;
  height: 24px;
  position: relative;
  cursor: pointer;
  display: none;
}
.header .burger span {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 100%;
  height: 2px;
  background-color: #b0c468;
}
.header .burger span:nth-of-type(1) {
  bottom: auto;
}
.header .burger span:nth-of-type(3) {
  top: auto;
}
.header .mobile-menu {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  width: 80%;
  min-height: 100vh;
  max-height: 100%;
  overflow: auto;
  background-color: #2d3336;
  z-index: 3;
  padding: 60px 20px;
}
.header .mobile-menu__wrap {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.header .mobile-menu__close {
  position: absolute;
  right: 20px;
  top: 20px;
  width: 24px;
  height: 24px;
}
.header .mobile-menu__close:after,
.header .mobile-menu__close:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #b0c468;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}
.header .mobile-menu__close:before {
  transform: rotate(-45deg);
}
.header .mobile-menu__close:after {
  transform: rotate(45deg);
}
.header .mobile-menu .menu {
  margin-bottom: 40px;
}
.header .mobile-menu .phone {
  display: block;
  margin-bottom: 20px;
}
.header .mobile-menu .btn {
  display: block;
  margin-left: 0;
}
@media screen and (max-width: 1200px) {
  .header .menu.desktop .menu__item:not(:last-of-type) {
    margin-right: 0;
  }
}
@media screen and (max-width: 992px) {
  .header {
    padding: 14px 0;
  }
  .header .menu.desktop {
    display: none;
  }
  .header .phone {
    display: none;
  }
  .header .btn {
    display: none;
  }
  .header .burger {
    display: block;
  }
}
